import styled from "styled-components";
import Edsection from "./Edsection";
const Container = styled.div`
  text-align: center;
  font-size: 28px;
  font-weight: 300;
  line-height: 36px;
  strong {
    font-weight: 600;
  }
`;
const Section = styled.div`
  padding: 70px 20px 70px;
  box-sizing: border-box;
  :nth-child(2n) {
    background-color: rgb(247, 247, 247);
  }
  :last-child {
    padding: 70px 0 0;
  }
`;
const Videobox = styled.div`
  display: flex;
  justify-content: center;
  iframe {
    width: 40%;
    height: 350px;
  }
  @media screen and (max-width: 1000px) {
    flex-direction: column;
    iframe {
      width: 100%;
    }
    iframe + iframe {
      padding-left: 0px;
      margin-top: 15px;
    }
  }
`;
const Textbox = styled.div`
  margin: 0 10px;
  padding: 50px 0px 50px 30px;
  box-sizing: border-box;
  white-space: pre-wrap;
  font-size: 15px;
  line-height: 150%;
  text-align: left;
  color: #333;
  .title {
    font-size: 22px;
    line-height: 35px;
    font-weight: 500;
  }
  @media screen and (max-width: 1000px) {
    text-align: center;
    padding-bottom: 0px;
    .title {
      font-size: 22px;
      margin-bottom: 10px;
    }
  }
`;
const Group = () => {
  return (
    <Container>
      <Section>
            
        <br />    <strong>  !</strong>
        <br />       .
      </Section>
      <Section>
        <Videobox>
          <iframe
            src="https://www.youtube.com/embed/2QQv1_ngYtA"
            title="YouTube video player"
            frameBorder="0"
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
            allowFullScreen
          ></iframe>
          <Textbox>
            <h1 className="title"> ? </h1>
                   !
            <br />
                     
            <br />
                   .
            <br />
                 , , ,
             <br />
              ,      
            
            <br />
               .
            <br />
                     
            <br />
              .
          </Textbox>
        </Videobox>
      </Section>
      <Section>
        <Edsection></Edsection>
      </Section>
    </Container>
  );
};

export default Group;
