import styled from "styled-components";
import Icon from "../atoms/Icon";
import { indexlessonicon } from "../data/homepage/icons";
const IndexLesson = () => {
  return (
    <Container>
      <Title>
        <h1>
           
          <br />
          <span></span> 
          <br />
           
        </h1>
        <h2>
                 .
          <br />        
          <br />
              .
        </h2>
      </Title>
      <Iconbox>
        {indexlessonicon.map((item) => (
          <Iconwrapper key={item.name}>
            <Icon icon={item.icon} />
            <h1>{item.name}</h1>
            <h1>{item.text}</h1>
          </Iconwrapper>
        ))}
      </Iconbox>
    </Container>
  );
};
const Container = styled.div`
  box-sizing: border-box;
  padding: 100px 150px 0px;
  background-color: rgba(7, 28, 63, 0.7);
  color: white;
  height: 100vh;
  position: relative;
  overflow: hidden;
  @media screen and (max-width: 1000px) {
    height: 200vh;
    padding: 0px;
    text-align: center;
  }
`;

const Title = styled.div`
  word-break: keep-all;
  font-size: 40px;
  h1 {
    display: inline-block;
    line-height: 120%;
  }
  span {
    color: #269cd3;
  }
  h2 {
    font-size: 16px;
    padding: 20px 0;
    line-height: 120%;
  }
  @media screen and (max-width: 1000px) {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
`;
const Iconbox = styled.div`
  display: flex;
  justify-content: space-between;
  padding-top: 50px;
  box-sizing: border-box;
  /* align-items: center; */
  @media screen and (max-width: 1000px) {
    padding: 20px 0;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
`;
const Iconwrapper = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 400px;
  padding: 15px;
  text-align: center;
  box-sizing: border-box;
  cursor: pointer;
  & path {
    fill: white;
  }
  svg {
    fill: "white";
    width: 100%;
    height: 200px;
  }
  h1 {
    width: 60%;
    word-break: keep-all;
    padding-top: 5px;
    font-size: 19px;
  }
  & h1 + h1 {
    font-size: 16px;
    padding-top: 10px;
  }
  &:hover h1,
  &:hover path {
    fill: #269cd3;
    color: #269cd3;
  }
  @media screen and (max-width: 1000px) {
    height: 33%;
    svg {
      height: 100%;
    }
    div {
      height: 50%;
    }
  }
`;
export default IndexLesson;
