import styled from "styled-components";

const Container = styled.div`
  display: flex;
  justify-content: center;
`;
const Box = styled.div`
  padding: 20px 50px;
  display: flex;
  width: 100%;
  justify-content: center;
  height: 100%;
`;
const Text = styled.div`
  white-space: pre-wrap;
  word-break: keep-all;
  h1 {
    font-size: 28px;
    font-weight: 300;
    line-height: 32px;
  }
  h1 + h1 {
    padding-bottom: 20px;
  }
  p {
    color: #333;
    font-size: 15px;
    line-height: 20px;
  }
  span {
    color: #0c2a71;
  }
  strong {
    color: #0c2a71;
    font-weight: 600;
  }
`;
const Img = styled.div`
  img {
    height: 100%;
  }
`;

const Companey = () => {
  return (
    <Container>
      <Box>
        <Text>
          <h1></h1>
          <h1>
            <span>
                <strong></strong>
            </span>
            .
          </h1>
          <p>
            {`    .
      
         
          .
    ,
     ,
      .
       .
    
    
      ?
        
          .
     .
           
    .
,      
        .
        .
    
  ,  
     .

       .
      ,      
         .
        
     .`}
          </p>
        </Text>
        <Img>
          <img
            src={process.env.PUBLIC_URL + "/assets/homepage/companey0.png"}
            alt=""
          />
        </Img>
      </Box>
    </Container>
  );
};

export default Companey;
